	module.exports = {"data":{"markdownRemark":{"html":"<p>At the Yifu Building, he received a delegation from the Institute of Public Administration of the Taiwan University of Political Science. It seems that there is still a big gap in understanding between the two sides. The time for separation between the two sides is too long. Many people in Taiwan are estimated to be too low on the social level and economic situation of the mainland. Our problem in this regard is that the estimate for Taiwan is too high. Therefore, people from mainland China do not have any imbalances when they go to Taiwan. They feel disappointed. The people from Taiwan who come to the mainland often do not understand the situation. Some of them are \"sage\" and especially young students. There is a Taiwanese doctoral student who talks there, just like class, but there are many prejudices in the speech, empty and empty. It seems that the two sides are to strengthen communication and increase recognition.</p>\n<p>Go to the Academy of Social Sciences to open the Social Affairs Committee for \"Exploration and Controversy.\" Having discussed four points of view, the current academic research should pay attention to: First, the new growth point that appears in the development of real life; second, pay attention to the combination of various theories, that is, interdisciplinary theoretical research, do not become a single subject The magazines can only do those professional magazines; the third is to determine the convergence point of readers' interest; the fourth is to pay attention to the collision points of domestic and foreign cultures. The choice of these four aspects is to determine the methodology of the topic. In fact, only a good topic can be determined before a good article can be found. The success of an article often depends on how the topic is chosen. We must also pay attention to ourselves in our research.</p>\n<p>When I went out, I forgot to bring the money. When I got to the car, I found out that I finally found some coins and went to the Academy of Social Sciences. This suddenly realized the importance of money in today's society, or it might not be easy. Society has increasingly become a monetary society, which is a cultural reaction inevitably brought about by economic development. When everyone was meeting today, they were still talking about the fact that Shanghai was a paradise for adventurers in the past, but now Shanghai is full of adventurers, including blind-flowing \"adventurers.\"</p>\n<p>There is a big change in the outside world. It can be said that \"the world outside is very exciting.\" It is a construction site everywhere, and the house is demolished. There will be a big change in two or three days. If urban construction develops like this, it will change a lot in a few years. After a few years, Shanghai will be impressive.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0064.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0064.md","frontmatter":{"title":"Friday, April 1st"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0064.md absPath of file >>> MarkdownRemark"}}

